.skills {
  width: 100%;
  /* background-color: var(--light-2); */
}

h1 {
  text-align: center;
}

.skills_container {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 0 auto;
}

.skills_wrapper {
  display: flex;
  flex-wrap: wrap;
}

.skill {
  padding: 10px 15px;
  margin-bottom: 25px;
  margin-right: 25px;
  border-radius: 5px;
  font-weight: 600;
  background-color: var(--white);
  background-color: var(--midnight-express);
  color: var(--purple);
  opacity: 85%;
}

.skill:hover {
  opacity: 0.7;
  background-color: var(--purple);
  color: var(--light);
}

.skills_cards {
  margin-bottom: 24px;
}

